.Axis path,
.Axis line {
  stroke: #E0E0E0;
}

.Axis text {
  font-size: 14px;
  fill: black;
}

.Axis-Bottom text {
  -webkit-transform: rotate(-40deg);
      -ms-transform: rotate(-40deg);
          transform: rotate(-40deg);
  text-anchor: end;
}


